<template>
  <div class="flex">
    <aside>
        <Sidebar :list="list" base="tracking" title="随钻跟踪"/>
    </aside>
    <section class="example">
        <router-view :route="route"/>
        <button class="full_screen" type="button" @click="toggle">{{screenSize}}</button>
    </section>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Sidebar from '@/components/common/Sidebar';
export default {
  data () {
    return {
        list: [
            {
                txt: '工程预警',
                url: 'earlywarning'
            },
            {
                txt: '工程参数监控',
                url: 'parameter'
            },
            {
                txt: '视频监控',
                url: 'videomonitor'
            },
            {
                txt: '视频会议',
                url: 'fluidcurve'
            },
            {
                txt: '三压力监测',
                url: 'wellcomplex'
            },
            {
                txt: '工作效率',
                url: 'work'
            }
        ],
        screenSize: '全屏'
    }
  },

  components: {
      Sidebar
  },

  computed: {
      ...mapGetters({
      route: 'route'
    }),
  },

  mounted: function() {},

  methods: {
    toggle () {
      this.$fullscreen.toggle(this.$el.querySelector('.example'), {
        wrap: false,
        callback: this.fullscreenChange
      })
    },
    fullscreenChange (fullscreen) {
      console.log(fullscreen);
      this.fullscreen = fullscreen;
      fullscreen?this.show("退出全屏") : this.show("全屏");
    },
    show(param) {
        this.screenSize =param;
    },
  }
}
</script>

<style lang='scss' scoped>
.flex {
    height: 100%;
}
aside {
    width: 260px;
}
section {
    width: calc(100% - 260px);
    position: relative;
    .full_screen {
        font-size: 12px;
        padding: 5px;
        color: yellow;
        background-color:rgba(0,0,0,0.4);
        position: absolute;
        bottom: 0;
        right: 0;
        z-index: 10;
        cursor: pointer;
        border: none;
        outline:none; 
    }
}
</style>



